body {
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  .login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  input[type=password] {
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    text-align: center;
  }
  
  button[type=submit] {
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
  }
  
  @media (min-width: 768px) {
    /* Styles for tablets and larger screens */
    .login-container {
      height: unset;
    }
  }

  body {
    display: flex;
    height: 100vh;
  
    align-items: center;
    justify-content: center;
  }
  
  .btn-liquid {
    display: inline-block;
    position: relative;
    width: 240px;
    height: 60px;
  
    border-radius: 27px;
  
    color: #fff;
    font: 700 14px/60px "Droid Sans", sans-serif;
    letter-spacing: 0.05em;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
  }
  
  .btn-liquid .inner {
    position: relative;
  
    z-index: 2;
  }
  
  .btn-liquid canvas {
    position: absolute;
    top: -50px;
    right: -50px;
    bottom: -50px;
    left: -50px;
  
    z-index: 1;
  }